.waves {
  position:relative;
  width: 100%;
  height:15vh;
  margin-bottom:-7px; /*Fix for safari gap*/
  min-height:100px;
  max-height:150px;
}

.waves-animation > use {
  animation: move-forever 25s cubic-bezier(.55,.5,.45,.5)     infinite;
}
.waves-animation > use:nth-child(1) {
  animation-delay: -2s;
  animation-duration: 7s;
}
.waves-animation > use:nth-child(2) {
  animation-delay: -3s;
  animation-duration: 10s;
}
.waves-animation > use:nth-child(3) {
  animation-delay: -4s;
  animation-duration: 13s;
}
.waves-animation > use:nth-child(4) {
  animation-delay: -5s;
  animation-duration: 20s;
}
@keyframes move-forever {
  0% {
   transform: translate3d(-90px,0,0);
  }
  100% {
    transform: translate3d(85px,0,0);
  }
}
