@keyframes colorChange {
  0% {
    background: radial-gradient(circle, #f6adad 0%, #be6d6d 25%, #a63939 50%, #771b1b 100%); }
  25% {
    background: radial-gradient(circle, #faa57b 0%, #be846d 25%, #a65a39 50%, #4d2802 100%); }
  50% {
    background: radial-gradient(circle, #fad07b 0%, #bea96d 25%, #a68639 50%, #887a15 100%); }
  100% {
    background: radial-gradient(circle, #f2f039 0%, #c8c358 25%, #d0ba23 50%, #aba411 100%); } }

@keyframes colorChangeAnimistaLight {
  0% {
    background: #19dcea; }
  25% {
    background: #b22cff; }
  50% {
    background: #ea2222; }
  75% {
    background: #f5be10; }
  100% {
