  @media (min-width: 767.98px) {
    .link-on-top:hover, .link-on-top:focus, .link-on-top:active {
      opacity: 1; }
      .link-on-top:hover .link-to-top__ball, .link-on-top:focus .link-to-top__ball, .link-on-top:active .link-to-top__ball {
        animation: bounce-top 0.9s both; }
      .link-on-top:hover::after, .link-on-top:focus::after, .link-on-top:active::after {
        background: rgba(0, 0, 0, 0.5); } }

